@media (min-width: 576px) {
  body #root header {
    display: flex;
    width: 100%;
  }
  body #root header .conteneur-header-logo-nav {
    display: flex;
    padding: 20px;
    width: 100%;
  }
  body #root header .conteneur-header-logo-nav .kasaLogo {
    width: 50%;
    height: 63px;
  }
  body #root header .conteneur-header-logo-nav .nav-header {
    display: flex;
    gap: 0;
    width: 50%;
    align-items: center;
    justify-content: space-around;
  }
  body #root header .conteneur-header-logo-nav .nav-header a {
    text-decoration: none;
    color: black;
  }
  body #root main {
    padding: 20px;
    height: auto;
  }
  body #root main .aProposCard {
    width: 100%;
  }
  body #root main .aProposCard img {
    width: 100%;
    height: 111px;
    object-fit: cover;
    object-position: center;
    filter: brightness(50%);
    border-radius: 10px;
  }
  body #root main .aProposDropDownSection {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    gap: 20px;
  }
  body #root main .aProposDropDownSection button {
    width: 100%;
    color: white;
    background-color: #FF6060;
    border-radius: 5px;
    gap: 20px;
  }
}
